<template>
  <div id="app">
    <div class="nav">
      <el-row >
        <el-col :span="12" :offset="6"><div class="grid-content bg-purple"></div>
          <router-link to='/'> <el-button plain>经纬度间距离</el-button></router-link>
          <router-link to='/regular'> <el-button plain>常用正则</el-button></router-link>
          <router-link to='/timeStamp'> <el-button plain>时间戳时间互转</el-button></router-link>
          <router-link to='/qrcode'> <el-button plain>生成二维码</el-button></router-link>
        </el-col>
      </el-row>
      <span class="appreciate" @click="getDialog">赞赏支持</span>
    </div>
    <router-view/>
    <el-dialog
      title="赞赏"
      :visible.sync="appreciateVisible"
      width="20%"
      center
      :before-close="handleClose">
      <img src="./assets/weChat.png" alt="">
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return{
      appreciateVisible: false
    }
  },
  methods: {
    getDialog(){
      this.appreciateVisible = true;
    },
    handleClose() {
      this.appreciateVisible = false;
    }
  }
}
</script>

<style lang="scss" scoped>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  .nav{
    height: 80px;
    line-height: 80px;
    border-bottom: 1px solid #d0cccc;
    position: fixed;
    left: 0;
    right: 0;
    z-index: 9999999;
    position: relative;
  }
  .el-row{
    background: #fff;
  }
  .appreciate{
    position: absolute;
    right: 30px;
    top: 0px;
    cursor: pointer;
    color: #ed7961;
  }
  .el-dialog{
    img{
      width: 200px;
      display: block;
      margin: 0 auto;
    }
  }
}
</style>



